
.kiko-message {
  position: fixed;
  padding: 10px;
  color: #fff;
  background-color: #5F6161;
  border-radius: 4px;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 999999999
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}

.kiko-tool-tip {
    display: block;
    position: absolute;
    position: fixed;
    background-color: #3695CC;
    padding: 10px 10px;
    border-radius: 5px;
    color: #fff;
    white-space: nowrap;
/*    word-wrap: break-word;*/
    z-index: 99999999
}
.kiko-tool-tip.left {
    -webkit-transform: translate(-100%, -50%);
            transform: translate(-100%, -50%);
}
.kiko-tool-tip.right {
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.kiko-tool-tip.top {
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
}
.kiko-tool-tip.bottom {
    -webkit-transform: translate(-50%, 100%);
            transform: translate(-50%, 100%);
}
.kiko-tool-tip.right .arrow {
    display: inline-block;
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    top: 50%;
    left: -10px;
    border-top: 10px solid transparent;
    /*border-left: 10px solid yellow;*/
    border-right: 15px solid #3695CC;
    border-bottom: 10px solid transparent;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.kiko-tool-tip.left .arrow {
    display: inline-block;
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    top: 50%;
    right: -10px;
    border-top: 10px solid transparent;
    border-left: 15px solid #3695CC;
    border-bottom: 10px solid transparent;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.kiko-tool-tip.top .arrow {
    display: inline-block;
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    left: 50%;
    bottom: -10px;
    border-top: 15px solid #3695CC;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}
.kiko-tool-tip.bottom .arrow {
    display: inline-block;
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    left: 50%;
    top: -10px;
    border-bottom: 15px solid #3695CC;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.bounce-enter-active {
 -webkit-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.bounce-leave-active {
  -webkit-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
@-webkit-keyframes bounce-in {
  -webkit-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
@keyframes bounce-in {
  -webkit-transform: scale(2);
  transform: scale(2);
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.kiko-confirm {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999999;
}
.kiko-confirm .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  /*background-color: #FFFFFF;*/
  background-color: #000;
  opacity: 0.3;
  z-index: 99999;
}
.kiko-confirm .kiko-container {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 260px;
  min-width: 260px;
  z-index: 9999999999999;
}
.kiko-confirm .kiko-container .header {
  width: 100%;
  padding: 10px;
  background-color: #4575A6;
  border-bottom: 1px solid #eee;
  color: #fff;
  text-align: left;
}
.kiko-confirm .kiko-container .content {
  width: 100%;
  padding: 10px;
  background-color: #FFFFFF;
}
.kiko-confirm .kiko-container .kiko-footer {
  width: 100%;
  padding: 10px;
  background-color: #FFFFFF;
  text-align: right;
  border-top: 1px solid #eee;
}
.kiko-btn {
  padding: 5px 15px;
  text-decoration: none !important;
  border-radius: 2px;
  margin: 6px 6px 0 6px;
}
.kiko-btn:hover {
  text-decoration:  none !important;
}
.kiko-btn:active {
  text-decoration:  none !important;
}
.kiko-btn.make-sure {
  background-color: #4575A6;
  color: #fff;
}
.kiko-btn.cancel {
  background-color: #BBB5B5;
  color: #fff;
}
.kiko-close-btn {
  cursor: pointer;
  float: right;
}
