.kiko-loading {
  color: #45B0FF;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.kiko-loading .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0.7;
  z-index: 999;
}
.kiko-loading .kiko-container {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 9999999999999;
}

* {
  margin: 0;
  padding: 0;
}
body,
html,
#app,
#mydiv {
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#mydiv {
  background-image: url(/magic/dist/static/img/demo-1-bg.c64d825.jpg);
  position: relative;
  width: 100%;
  background-color: #333;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
}
#mydiv canvas {
  z-index: 0!important;
}
.content {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -250px;
  margin-left: -400px;
  z-index: 2;
  width: 800px;
  height: 500px;
  background: rgba(0, 0, 0, .5);
  -webkit-box-shadow: 0 0 5px 10px rgba(0, 0, 0, .5);
          box-shadow: 0 0 5px 10px rgba(0, 0, 0, .5);
  border-radius: 5px;
  padding: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.btn-box {
  margin-top: 30px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.btn-item {
  width: 200px;
  border-radius: 10px;
  color: blue;
  text-align: center;
  line-height: 50px;
  background: rgba(0, 0, 0, .5);
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  cursor: pointer;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
